"use client"

import { useState, useEffect } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"

interface StudentFormModalProps {
  open: boolean
  onClose: () => void
  onSubmit: (data: any) => void
  initialData?: any
  title: string
}

export function StudentFormModal({ open, onClose, onSubmit, initialData, title }: StudentFormModalProps) {
  const [form, setForm] = useState<any>({})

  useEffect(() => {
    setForm(initialData || {})
  }, [initialData])

  const handleChange = (key: string, value: any) => setForm((prev: any) => ({ ...prev, [key]: value }))

  const handleSubmit = () => onSubmit(form)

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-3xl max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>{title}</DialogTitle>
        </DialogHeader>
        <div className="grid grid-cols-1 gap-3">
          <Input placeholder="AdmissionNo" value={form.AdmissionNo || ""} onChange={(e) => handleChange("AdmissionNo", e.target.value)} />
          <Input placeholder="Name" value={form.student_name || ""} onChange={(e) => handleChange("student_name", e.target.value)} />
          <Input placeholder="Father Name" value={form.ParentNameFather || ""} onChange={(e) => handleChange("ParentNameFather", e.target.value)} />
          <Input placeholder="Mother Name" value={form.Mother || ""} onChange={(e) => handleChange("Mother", e.target.value)} />
          <Input placeholder="Class" value={form.SchoolClass || ""} onChange={(e) => handleChange("SchoolClass", e.target.value)} />
          <Input placeholder="Mobile" value={form.mobile || ""} onChange={(e) => handleChange("mobile", e.target.value)} />
          <Input placeholder="Email" value={form.email || ""} onChange={(e) => handleChange("email", e.target.value)} />
          <Input placeholder="Address" value={form.address || ""} onChange={(e) => handleChange("address", e.target.value)} />
          <div className="flex justify-end mt-2">
            <Button onClick={handleSubmit}>{title.includes("Add") ? "Add" : "Update"}</Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default StudentFormModal